// bg
$mainBg: hsl(218, 28%, 13%);
$heroBg: hsl(217, 28%, 15%);
$feedbackBg: hsl(219, 30%, 18%);
$footerBg: hsl(216, 53%, 9%);

// colors
$cyan: hsl(176, 68%, 64%);
$blue: hsl(198, 60%, 50%);
$white: hsl(0, 0%, 100%);

// fonts
$raleway: 'Raleway', sans-serif;
$opensans: 'Open Sans', sans-serif;

// font weights
$normal: 400;
$bold: 700;

*,
*::after,
*::before {
  box-sizing: border-box;
}

html {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  color: $white;
}

main {
  background: $mainBg;
}

footer {
  background: $footerBg;
  color: $white;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

// nav

nav {
  background: $heroBg;

  .inner-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 0;
  }

  .nav-links {
    a {
      text-decoration: none;
      font-family: $raleway;
      font-weight: $normal;
      color: $white;
      display: inline-block;
      &:not(:last-child) {
        margin-right: 2.5rem;
      }
    }
  }
}

// hero
.hero {
  padding: 1rem 0 5rem 0;
  background: $heroBg;
  color: $white;

  .hero-img-container {
    width: 500px;
    z-index: 9;

    img {
      width: 100%;
      height: auto;
    }
  }

  .inner-hero {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 9;
  }

  .hero-title {
    font-family: $raleway;
    margin-top: 0;
    max-width: 500px;
    text-align: center;
    font-size: 2rem;
    line-height: 1.2;
    z-index: 9;
  }

  .hero-subtitle {
    max-width: 500px;
    font-size: 1rem;
    text-align: center;
    margin-bottom: 2rem;
    z-index: 9;
  }
}

// button
.btn {
  cursor: pointer;
  border: none;
  outline: none;
  background: linear-gradient(to bottom right, $cyan, $blue);
  color: $white;
  font-family: $raleway;
  font-weight: $bold;
  padding: 16px;
  text-align: center;
  border-radius: 25px;
  min-width: 200px;
  z-index: 9;
}

.btn-link {
  cursor: pointer;
  border: none;
  outline: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: transparent;
  font-family: $raleway;
  font-weight: $bold;
  font-size: 1rem;
  color: $cyan;

  span {
    display: inline-flex;
    align-items: center;
    margin-left: 8px;
  }
}

// features
.features {
  position: relative;
  width: 100%;
  padding: 5rem 0;

  .wave {
    position: absolute;
    top: -60%;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: auto;
      z-index: -1;
    }
  }

  .inner-features {
    padding: 0 3rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
    grid-gap: 3rem;

    .feature {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      padding: 1rem 2.5rem;

      h2 {
        margin-bottom: 0;
      }

      p {
        text-align: center;
        padding: 0 2rem;
      }
    }
  }
}

// sect
.sect {
  padding: 5rem 0;
  .inner-sect {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .sect-details {
      padding: 0 4rem;
      h1 {
        font-size: 2.5rem;
        font-family: $raleway;
      }

      p {
        font-size: 1rem;
      }

      p:last-of-type {
        margin-bottom: 2rem;
      }
    }
  }
}

// feedbacks
.feedbacks {
  padding: 5rem 0 20rem 0;

  .inner-feedbacks {
    display: flex;
    justify-content: center;
    align-items: center;

    position: relative;

    .quote {
      position: absolute;
      top: -35px;
      left: 5px;
      z-index: 0;
    }
  }

  .feedback-item {
    display: flex;
    flex-direction: column;
    margin: 0 1rem;
    padding: 2rem;
    background: $feedbackBg;
    border-radius: 4px;
    box-shadow: 10px 10px hsla(219, 30%, 18%, 0.2);
    z-index: 9;

    .feedback {
      font-size: 0.875rem;
      margin-bottom: 2rem;
    }

    .profile-details {
      display: flex;
      align-items: center;

      .profile-image-container {
        width: 42px;
        height: 42px;
        img {
          width: 100%;
          height: auto;
          border-radius: 50%;
        }
      }

      .profile-name {
        margin-left: 1rem;
        display: flex;
        flex-direction: column;

        p {
          margin: 0;

          &:first-child {
            font-weight: $bold;
          }

          &:last-child {
            font-size: 12px;
          }
        }
      }
    }
  }
}

// footer
footer {
  padding: 8rem 0;
  position: relative;

  .footer-blocks {
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // background: pink;

    .footer-location {
      display: flex;
      align-items: center;

      p {
        max-width: 300px;
        margin-left: 2rem;
      }
    }

    .footer-contacts {
      div {
        display: flex;
        align-items: center;

        p {
          margin-left: 2rem;
        }
      }
    }

    .footer-links {
      display: flex;
      flex-direction: column;
      a {
        display: inline-block;
        text-decoration: none;
        color: $white;
        margin-bottom: 0.875rem;
      }
    }

    .footer-socials {
      display: flex;

      a {
        text-decoration: none;
        color: $white;
        display: inline-block;
        border-radius: 50%;

        :not(:last-child) {
          margin-right: 1rem;
        }

        i {
          width: 36px;
          height: 36px;
          display: inline-flex;
          justify-content: center;
          align-items: center;
          font-size: 1rem;
          border-radius: 50%;
          border: 1px solid $white;
        }
      }
    }
  }
}

// email form
.email-form {
  position: relative;
  padding: 5rem 0;

  .inner-email-form {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .email-container {
    position: absolute;
    top: -150%;
    left: 50%;
    display: flex;
    width: 850px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem 0;
    background: $heroBg;
    border-radius: 4px;
    box-shadow: 3px 10px 10px rgba(0, 0, 0, 0.15);
    transform: translateX(-50%);

    h1 {
      font-family: $raleway;
    }

    p {
      max-width: 70%;
      text-align: center;
      margin-bottom: 2rem;
      line-height: 1.25rem;
    }

    input[type='text'] {
      outline: none;
      border: none;
      display: inline-block;
      width: 500px;
      border-radius: 25px;
      padding: 0.8rem 1rem;
      font-size: 1rem;
    }

    button {
      margin-left: 1rem;
    }
  }
}
